        padding: 1rem;
        color: var(--tertiary-color);
    }

    .Portfolio__item-container:hover {
        background-color: rgb(var(--primary-color-rgb) / 0.5);
    }

    .Portfolio__item-image {
    }

    .Portfolio__item-title {
        font-size: 3rem;
        text-align: center;
    }

    .Portfolio__item-description {
        font-size: 2rem;
        text-align: center;
    }

    .Portfolio__item-link-container {
        column-gap: 1rem;
    }

    .Portfolio__item-link {
        color: var(--tertiary-color);
    }

    .Portfolio__item-link svg {
        width: 35px;
        height: 35px;
    }

    .Footer {
    }

    .Footer__back-to-top {
    }

    .Footer__back-top-top-arrow {
    }

    .Footer__links {
    }

    .Footer__links {
    }
    .Footer__links img {
        width: 34px;
    }
}
